export default {
  methods: {
    /**
     * 刷新页面
     * @param {Object} view 路由对象
     */
    refreshView(view) {
      this.$store.dispatch('tagsView/delCachedView', view).then(() => {
        const { fullPath, query } = view;
        let url = fullPath;
        Object.keys(query).forEach(key => {
          url = this.$util.changeURLPar(url, key, query[key]);
        });
        this.$nextTick(() => {
          this.$router.replace({
            path: '/redirect' + url
          });
        });
      });
    }
  }
};
