import axios from '@/plugins/axios';

/**
 * 备授课服务
 * @param  {...any} args 参数列表
 */
const bskptService = function(...args) {
  args.splice(1, 0, 'bskpt-admin');
  return axios.apply(null, args);
};

/**
 * 获取菜单列表
 * @return {Promise}
 */
export function getMenuList() {
  return axios(
    {
      method: 'get',
      url: '/auth/findAuthByUserId'
    },
    'bonus-be'
  );
}

/**
 * 获取按钮列表
 * @return {Promise}
 */
export function getAuthList() {
  return axios(
    {
      method: 'GET',
      url: '/auth/getInterfaceById'
    },
    'bonus-be'
  );
}
