import { getMenuList, getAuthList } from '../../api/auth/index';
import utils from '@/plugins/common/utils';
import { routes, asyncRoutes } from '../../router/index';

const state = {
  // 路由配置
  routes: [],
  // 菜单列表
  menuList: [],
  // 菜单树
  menuTree: [],
  // 按钮权限列表
  authList: []
};

/**
 * 菜单列表转换树列表
 */
const menuListConvertMenuTree = menuList => {
  return utils.listConvertTree(menuList, 'menuId', 'parentMenuId', true);
};

/**
 * 排序菜单树
 * @param {Array} menuTree 菜单树
 */
const sortMenuTree = menuTree => {
  let sort = list => {
    list.sort((a, b) => {
      return a.orderNo - b.orderNo;
    });
    list.forEach(
      value => value.children && value.children.length && sort(value.children)
    );
    return list;
  };
  return sort(menuTree);
};

/**
 * 树型数据转列表
 * @param {*} tree 树型数据
 * @param {*} childrenKey 子节点列表键
 */
const treeConvertList = (tree, childrenKey = 'children') => {
  if (!Array.isArray(tree)) return [];
  let list = [];
  let fn = (data, parentMenuId = -1) => {
    list = list.concat(data.slice(0));
    data.forEach(item => {
      let children = item[childrenKey];
      item.url = item.url || item.path;
      item.meta = item.meta || {};
      item.menuName = item.menuName || item.meta.title;
      item.menuId = item.menuId || item.path;
      item.parentMenuId = item.parentMenuId || parentMenuId;
      if (Array.isArray(children)) {
        fn(children, item.menuId);
      }
    });
  };
  fn(tree);
  return list;
};

const mutations = {
  SET_ROUTES(state, routes) {
    state.routes = routes;
  },
  SET_MENU_LIST(state, menuList) {
    state.menuList = menuList;
  },
  SET_MENU_TREE(state, menuTree) {
    state.menuTree = menuTree;
  },
  SET_AUTH_LIST(state, authList) {
    state.authList = authList;
  }
};

const actions = {
  /**
   * 获取菜单列表
   */
  getMenuList({ commit }) {
    // return getMenuList().then(({ data }) => {
    return Promise.resolve().then(() => {
      // 首页路由配置
      let homeRoute = routes.find(item => item.path === '/');
      // 打平路由数据
      let routeList = treeConvertList(
        [homeRoute.children[0]].concat(asyncRoutes)
      );
      let data = routeList;
      let menuTree = menuListConvertMenuTree(routeList); // 菜单列表转菜单树
      menuTree = sortMenuTree(menuTree); // 菜单排序
      commit('SET_MENU_LIST', data);
      commit('SET_MENU_TREE', menuTree);
      return data;
    });
  },
  /**
   * 获取按钮列表
   */
  getAuthList({ commit }) {
    return getAuthList().then(({ data }) => {
      let authList = data.map(item => item && item.itfCode);
      commit('SET_AUTH_LIST', authList);
      return data;
    });
  },
  /**
   * 获取权限数据
   */
  async getAuthData({ dispatch }) {
    let menuList = await dispatch('getMenuList');
    // let authList = await dispatch('getAuthList');

    return {
      menuList
    };
  }
};

export default {
  namespaced: true,
  state,
  mutations,
  actions
};
