        </el-dropdown-menu>
      </el-dropdown>
    </div>
    <div class="right-menu staff-name">{{ $store.getters.staffName }}</div>
    <div class="right-menu">
      <el-button
        v-if="enableHandoverService"
        size="mini"
        type="primary"
        style="margin-right: 8px;"
        @click="onClickSwitchService"
      >
        切换服务器
      </el-button>
    </div>
    <c-api-tools
      v-if="enableHandoverService"
      ref="cApiTools"
      servers="bonus-be"
    ></c-api-tools>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import CBreadcrumb from '../../../components/common/CBreadcrumb';
import CHamburger from '@/components/common/CHamburger';
import { MessageBox, Button } from 'element-ui';
import config from '../../../config';
import CApiTools from '@/components/common/CApiTools';

export default {
  components: {
    [Button.name]: Button,
    CBreadcrumb,
    CHamburger,
    CApiTools
  },
  data() {
    return {
      // 启用切换服务工具
      enableHandoverService: config.enableHandoverService
    };
  },
  computed: {
    ...mapGetters(['sidebar', 'avatar'])
  },
  methods: {
    toggleSideBar() {
      this.$store.dispatch('app/toggleSideBar');
    },
    async logout() {
      await this.$store.dispatch('user/logout');
      this.$store.commit('app/RESET_DATA_LOAD');
      this.$router.push(`/login?redirect=${this.$route.fullPath}`);
    },
    onClickSwitchService() {
      this.$refs.cApiTools.show();
    }
  }
};
</script>

