const originalPush = VueRouter.prototype.push;
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err);
};

const originalReplace = VueRouter.prototype.replace;
VueRouter.prototype.replace = function push(location) {
  return originalReplace.call(this, location).catch(err => err);
};

const createRouter = () =>
  new VueRouter({
    mode: 'history', // require service support
    base: process.env.VUE_APP_PUBLIC_PATH + 'bonus-tool-admin',
    scrollBehavior: () => ({ y: 0 }),
    routes: routes
  });

const router = createRouter();

// Detail see: https://github.com/vuejs/vue-router/issues/1234#issuecomment-357941465
export function resetRouter() {
  const newRouter = createRouter();
  router.matcher = newRouter.matcher; // reset router
}

/**
 * 生成路由
 * @param {*} menuList 菜单列表
 */
export function generateRoutes(menuList) {
  if (!Array.isArray(menuList)) menuList = [];
  let menuUrlList = menuList.map(item => item.url);
  let fn = function(arr) {
    let list = arr.filter(item => {
      // 公共页面，不受权限控制
      const meta = item.meta || {};
      if (meta.public) return true;

      let index = menuUrlList.indexOf(item.path); // 查找元素索引
      let res = index !== -1; // 是否存在
      res &&
        Array.isArray(item.children) &&
        item.children.length &&
        (item.children = fn(item.children));
      // 替换路由配置的meta.title
      if (res) item.meta.title = menuList[index].menuName;
      return res;
    });
    return list;
  };
  let tree = fn(asyncRoutes);
  // 添加404路由配置，因为*匹配需要放最后面。
  // tree.push(asyncRoutes[asyncRoutes.length - 1]);
  return tree;
}

export default router;
