import _Message2 from "element-ui/lib/theme-chalk/message.css";
import "element-ui/lib/theme-chalk/base.css";
import _Message from "element-ui/lib/message";
import Vue from 'vue';
import router from '../router';
import store from '../store';
import axiosWithHost, { axiosPlugin } from '@/plugins/axios.js';
var codeDealsMap = {
  'S|400': function S400(res) {
    _Message({
      message: res.message,
      type: 'warning',
      duration: 5 * 1000
    });
  },
  'S|403': function S403(res) {
    _Message({
      message: res.message,
      type: 'error',
      duration: 5 * 1000
    }); // 登出


    store.dispatch('user/logout').then(function () {
      router.push({
        name: 'Login'
      });
    });
  },
  'S|500': function S500(res) {
    _Message({
      message: res.message,
      type: 'error',
      duration: 5 * 1000
    });
  },
  'E|404': function E404(err, message) {
    // router.push('/student/new-student');
    _Message({
      message: message,
      type: 'error',
      duration: 5 * 1000
    });
  },
  S: function S(_ref) {
    var message = _ref.message;

    _Message({
      message: message,
      type: 'error',
      duration: 5 * 1000
    });
  },
  E: function E(err, message) {
    _Message({
      message: message,
      type: 'error',
      duration: 5 * 1000
    });
  }
};
Vue.use(axiosPlugin, codeDealsMap);
export default axiosWithHost;