/**
 * 按钮权限指令
 * 根据vuex下的auth模块的authList按钮code列表判断权限
 */
import Vue from 'vue';
import store from '../../store';
import config from '../../config';

/**
 * 校验是否有权限，无权限删除dom节点
 * @param {*} el dom元素
 * @param {*} binding 绑定数据
 */
function has(el, binding) {
  // 不启用按钮权限功能
  if (!config.enabledButtonAuth) return;
  const { value } = binding;
  const authList = (store.getters && store.getters.authList) || [];
  if (value && value instanceof Array && value.length > 0) {
    const permissionrules = value;

    const hasPermission = authList.some(role => {
      return permissionrules.indexOf(role) >= 0;
    });

    if (!hasPermission && value && value.length) {
      el.parentNode && el.parentNode.removeChild(el);
    }
  }
}

let auth = {
  inserted: has,
  update: has
};

const install = function(Vue) {
  Vue.directive('auth', auth);
};

Vue.use(install);
