import { queryDictList } from '../../api/dict/index';
import { has } from 'lodash';

// 字典数据
const state = {
  SC: [], //食材
  BUYER_ORDER_STATUS: [], //采购方状态
  SUPPLIER_ORDER_STATUS: [], //供应商状态
  ORDER_TYPE: [], //订单性质
  MEAL_TYPE: [], //餐别类型
  BUYER_OPERATION_STATUS: [], //操作状态
  SUPPLIER_OPERATION_STATUS: [] //操作状态
};

// 获取方法
const getters = {
  /**
   * 根据type获取字典列表数据
   * 如果是获取大于两层字典根据层级使用逗号隔开
   * @param {String} type 字典类型
   * @return {Array}
   */
  getList: state => type => getList(state, type),
  /**
   * 根据传递type和key查询字典翻译
   * @param {type: String, key: String} 参数
   * @return {String}
   */
  queryLabel: state => options => {
    if (typeof options !== 'object' || options === null) return '';
    var type = options.type;
    var list = getList(state, type);
    var key = options.key;
    for (const item of list) {
      if (item.value === key) return item.label;
    }
    return '';
  }
};

const mutations = {
  /**
   * 设置字典数据
   * @param {*} state 状态
   * @param {*} map map数据
   */
  SET_DICT_MAP(state, map) {
    // 匹配有的进行赋值
    Object.keys(state).forEach(key => {
      has(map, key) && (state[key] = map[key]);
    });
  }
};

const actions = {
  /**
   * 请求获取字典数据
   * @param {*} param0 状态
   * @returns {Promise}
   */
  ajaxGetData({ state, commit }) {
    return queryDictList({
      keyList: Object.keys(state) // 要查询的字典
    }).then(({ data }) => {
      // 将list转map结构便于查找赋值
      let map = {};
      Array.isArray(data) &&
        data.forEach(
          item =>
            (map[item.dictionaryKey] = list2options(
              item.children,
              'dictionaryKey',
              'dictionaryText'
            ))
        );
      // 设置值
      commit('SET_DICT_MAP', map);
    });
  }
};

/**
 * 获取字典列表
 * @param {*} state 状态
 * @param {*} type 类型
 */
const getList = (state, type) => {
  type = type || '';
  var types = type.split(',');
  if (types.length > 1) {
    var list = has(state, types[0]) ? state[types[0]] : [];
    for (var i = 1; i < types.length; i++) {
      var val = types[i];
      var res = list.find(function(value) {
        return value.key == val;
      });
      if (res !== void 0) list = res.children || [];
      else {
        list = [];
        break;
      }
    }
    return list;
  } else {
    return has(state, type) ? state[type] : [];
  }
};

/**
 * 将list转为默认的options数据
 * @param {*} list 源数据列表
 */
function list2options(
  list,
  keyKey = 'dictionaryKey',
  labelKey = 'dictionaryText'
) {
  if (!Array.isArray(list)) return [];
  return list.map(function(item) {
    if (item.children && item.children.length)
      item.children = list2options(item.children, keyKey, labelKey);
    return {
      value: item[keyKey],
      label: item[labelKey]
    };
  });
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
};
