// vue 过滤器
import Vue from 'vue';

const formatNullValue = function(val) {
  return !val && val !== 0 ? '-' : val;
};

const formatAmountValue = function(val) {
  //空值异常处理
  if (val === null || val === undefined) {
    return '-';
  }
  return val.toLocaleString();
};

const formatBooleanValue = function(val) {
  let res;
  if (val === null || val === undefined) {
    res = '-';
  } else {
    res = val === true ? '是' : '否';
  }
  return res;
};

/**格式化切配标准 */
const formatCutMatchStandard = function(item) {
  if (!item) return '-';
  let str = `(${item.cutMatchStandardLength ||
    '无'}*${item.cutMatchStandardWidth || '无'}*${item.cutMatchStandardHeight ||
    '无'})`;
  return str;
};

const filters = {
  /**
   * 格式化字段为空时的数据转为"-"
   * @param {string} val
   */
  formatNullValue,
  /**
   * 格式化金额，千分位‘,’号隔开
   * @param {string} val
   */
  formatAmountValue,
  /**
   * 格式化数据为true和false的字段
   */
  formatBooleanValue,
  /**格式化切配标准 */
  formatCutMatchStandard
};

// 注册全局过滤器
for (let key in filters) {
  Vue.filter(key, filters[key]);
}

export {
  formatNullValue,
  formatAmountValue,
  formatBooleanValue,
  formatCutMatchStandard
};
