import { login, logout, getInfo } from '../../api/user/index';
import { getToken, setToken, removeToken } from '../../../../plugins/core/auth';
import { resetRouter } from '../../router';
import avatarGif from '../../assets/images/avatar.gif';

const getDefaultState = () => {
  return {
    token: getToken(),
    name: '',
    avatar: avatarGif,
    staffCode: '', // 工号
    staffName: '', // 用户名
    roleType: '' // 色类别：[] 1：系统管理员，2：集团管理员，3：采购员，4：审核员，5：学校管理员，6：总供应商；7：地区供应商
  };
};

const state = getDefaultState();

const mutations = {
  RESET_STATE: state => {
    Object.assign(state, getDefaultState());
  },
  SET_TOKEN: (state, token) => {
    state.token = token;
  },
  SET_NAME: (state, name) => {
    state.name = name;
  },
  SET_AVATAR: (state, avatar) => {
    state.avatar = avatar;
  },
  SET_STAFF_CODE: (state, staffCode) => {
    state.staffCode = staffCode;
  },
  SET_STAFF_NAME: (state, staffName) => {
    state.staffName = staffName;
  },
  SET_ROLE_TYPE: (state, roleType) => {
    state.roleType = roleType;
  }
};

const actions = {
  // user login
  login({ commit }, userInfo) {
    const { username, password } = userInfo;
    return new Promise((resolve, reject) => {
      login({ username: username.trim(), password: password })
        .then(response => {
          const { data } = response;
          commit('SET_TOKEN', data[0]);
          setToken(data.token);
          data.isFirstLogin !== undefined
            ? resolve(data.isFirstLogin)
            : resolve();
        })
        .catch(error => {
          reject(error);
        });
    });
  },

  // get user info
  getInfo({ commit, state }) {
    return new Promise((resolve, reject) => {
      getInfo(state.token)
        .then(response => {
          const { data } = response;

          if (!data) {
            return reject('Verification failed, please Login again.');
          }

          const { name, avatar, roleId, username, userId } = data;
          // commit('SET_NAME', name);
          // commit('SET_AVATAR', avatar);
          commit('SET_STAFF_CODE', userId);
          commit('SET_STAFF_NAME', username);
          commit('SET_ROLE_TYPE', roleId);
          resolve(data);
        })
        .catch(error => {
          reject(error);
        });
    });
  },

  // user logout
  logout({ commit, state }) {
    removeToken(); // must remove  token  first
    resetRouter();
    commit('RESET_STATE');
    // return new Promise((resolve, reject) => {
    //   logout(state.token)
    //     .then(() => {
    //       removeToken(); // must remove  token  first
    //       resetRouter();
    //       commit('RESET_STATE');
    //       resolve();
    //     })
    //     .catch(error => {
    //       reject(error);
    //     });
    // });
  },

  // remove token
  resetToken({ commit }) {
    return new Promise(resolve => {
      removeToken(); // must remove  token  first
      commit('RESET_STATE');
      resolve();
    });
  }
};

export default {
  namespaced: true,
  state,
  mutations,
  actions
};
