import router, { generateRoutes } from '../../router';
import store from '../../store';
import { MessageBox, Loading } from 'element-ui';
import NProgress from 'nprogress'; // progress bar
import 'nprogress/nprogress.css'; // progress bar style
import { getToken } from '../../../../plugins/core/auth'; // get token from cookie

NProgress.configure({ showSpinner: false }); // NProgress Configuration

const whiteList = ['/login', '/sso/auth']; // no redirect whitelist

router.beforeEach(async (to, from, next) => {
  // start progress bar
  NProgress.start();

  // set page title
  document.title = to.meta.title || store.state.settings.title;

  // determine whether the user has logged in
  const hasToken = getToken();
  if (hasToken) {
    if (to.path === '/login') {
      // if is logged in, redirect to the home page
      next({ path: '/' });
      NProgress.done(); // hack: https://github.com/PanJiaChen/vue-element-admin/pull/2939
    } else {
      // determine whether the user has obtained his permission roles through getInfo
      // const hasRoles = store.getters.roles && store.getters.roles.length > 0;
      const hasAppDataLoaded = !!store.getters.hasAppDataLoaded;
      if (hasAppDataLoaded) {
        next();
      } else {
        let loading = Loading.service({
          text: '努力加载数据中...'
        });
        try {
          // get user info
          // note: roles must be a object array! such as: ['admin'] or ,['developer','editor']
          await store.dispatch('user/getInfo'); // 加载用户信息

          // todo await store.dispatch('dict/ajaxGetData'); // 加载数据字典

          let { menuList } = await store.dispatch('auth/getAuthData'); // 获取权限数据

          // generate accessible routes map based on roles
          // const accessRoutes = await store.dispatch(
          //   'auth/generateRoutes',
          //   menuList
          // );
          const accessRoutes = generateRoutes(menuList); // 生成允许访问的路由
          // dynamically add accessible routes
          router.addRoutes(accessRoutes);

          store.commit('app/FINISH_DATA_LOAD'); // 数据加载完成

          // hack method to ensure that addRoutes is complete
          // set the replace: true, so the navigation will not leave a history record
          next({ ...to, replace: true });
          loading.close();
        } catch (error) {
          loading.close();
          // remove token and go to login page to re-login
          console.error(error);
          try {
            await MessageBox.confirm(
              '当前数据加载不稳定，请您重新登录或重新加载',
              '错误',
              {
                confirmButtonText: '重新加载数据',
                cancelButtonText: '登录页面',
                closeOnClickModal: false,
                closeOnPressEscape: false,
                showClose: false,
                type: 'error'
              }
            );
            // 重新加载数据
            next({
              ...to,
              replace: true
            });
          } catch (e) {
            console.error(e);
            // 登出
            await store.dispatch('user/resetToken');
            next(`/login?redirect=${to.path}`);
            NProgress.done();
          }
        }
      }
    }
  } else {
    /* has no token*/

    if (whiteList.indexOf(to.path) !== -1) {
      // in the free login whitelist, go directly
      next();
    } else {
      // other pages that do not have permission to access are redirected to the login page.
      next(`/login?redirect=${to.path}`);
      NProgress.done();
    }
  }
});

router.afterEach(() => {
  // finish progress bar
  NProgress.done();
});
