<template>
  <el-breadcrumb class="app-breadcrumb" separator="/">
    <transition-group name="breadcrumb">
      <el-breadcrumb-item v-for="(item, index) in levelList" :key="item.path">
        <span
          v-if="item.redirect === 'noRedirect' || index == levelList.length - 1"
          class="no-redirect"
          >{{ item.meta.title }}</span
        >
        <a v-else @click.prevent="onLink(item)">{{ item.meta.title }}</a>
      </el-breadcrumb-item>
    </transition-group>
  </el-breadcrumb>
</template>

<script>
import pathToRegexp from 'path-to-regexp';
import { mapGetters } from 'vuex';

export default {
  name: 'CBreadcrumb',
  data() {
    return {
      levelList: null
    };
  },
  computed: {
    ...mapGetters(['menuList'])
  },
  watch: {
    $route() {
      this.getBreadcrumb();
    }
  },
  created() {
    this.getBreadcrumb();
  },
  methods: {
    getBreadcrumb() {
      // console.log(this.getMenuMatched());
      // only show routes with meta.title
      // let matched = this.$route.matched.filter(
      //   item => item.meta && item.meta.title
      // );
      // 使用菜单匹配路由
      let matched = this.getMenuMatched();
      const first = matched[0];

      if (!this.isDashboard(first)) {
        matched = [{ path: '/home', meta: { title: '首页' } }].concat(matched);
      }
      this.levelList = matched.filter(
        item => item.meta && item.meta.title && item.meta.breadcrumb !== false
      );
    },
    isDashboard(route) {
      const name = route && route.name;
      if (!name) {
        return false;
      }
      return (
        name.trim().toLocaleLowerCase() === 'Dashboard'.toLocaleLowerCase()
      );
    },
    pathCompile(path) {
      // To solve this problem https://github.com/PanJiaChen/vue-element-admin/issues/561
      const { params } = this.$route;
      console.log(path);
      var toPath = pathToRegexp.compile(path);
      return toPath(params);
    },
    onLink(item) {
      const { redirect, path } = item;
      if (redirect) {
        this.$router.push(redirect);
        return;
      }
      this.$router.push(this.pathCompile(path));
    },
    /**
     * 获取菜单访问路径
     */
    getMenuMatched() {
      let path = this.$route.path;
      let menuList = this.menuList.slice(0); // 拷贝路由
      let menu = menuList.find(item => item.url === path);
      if (!menu) return []; // 未找到菜单
      let matched = [];
      // 向上查找父级菜单
      let fn = menuId => {
        let parentIndex = menuList.findIndex(item => item.menuId === menuId);
        if (parentIndex === -1) return; // 未找到上级菜单
        let parentMenu = menuList[parentIndex];
        menuList.splice(parentIndex, 1); // 防止死递归
        matched.unshift(parentMenu);
        parentMenu.menuId !== parentMenu.parentMenuId && fn(menu.parentMenuId); // 防止死递归
      };
      fn(menu.menuId);
      return matched;
    }
  }
};
</script>

<style lang="less" scoped>
.app-breadcrumb.el-breadcrumb {
  display: inline-block;
  font-size: 14px;
  line-height: 50px;
  margin-left: 8px;

  .no-redirect {
    color: #97a8be;
    cursor: text;
  }
}
</style>
