import Vue from 'vue';
import store from '../../store/index';

/**
 * 翻译字典
 * @param {*} key 值
 * @param {*} type 字典类型
 */
export function dict(...args) {
  if (args.length === 1) {
    // 获取字典列表
    return store.getters['dict/getList'](args[0]);
  } else {
    return store.getters['dict/queryLabel']({
      type: args[0],
      key: args[1]
    });
  }
}

// 挂载实例方法
Object.defineProperty(Vue.prototype, '$D', {
  value: dict
});
