<template>
  <div class="app-container">
    <!-- 管理端端 -->
    <!-- 登录 -->
    <div class="login-panel" v-if="!isFirstLogin">
      <div class="login-txt">海亮教育集团校园激励分配管理后台</div>

      <el-form
        ref="loginFormRef"
        :model="loginForm"
        :rules="rules"
        @keydown.native.enter.prevent="login"
      >
        <el-form-item prop="username">
          <el-input
            v-model="loginForm.username"
            placeholder="账号"
            maxlength="11"
            autofocus
            autocomplete="on"
          ></el-input>
        </el-form-item>
        <el-form-item prop="password">
          <el-input
            v-model="loginForm.password"
            type="password"
            autocomplete="on"
            placeholder="密码"
          ></el-input>
        </el-form-item>
        <!--submit button-->
        <el-form-item style="margin-top: 40px">
          <el-button
            type="primary"
            style="width: 100%; margin-top: 20px; background: #0076f6;"
            @click="login"
          >
            登录
          </el-button>
        </el-form-item>
        <!-- <el-form-item style="text-align: center;"> -->
        <p style="text-align:center;">
          <el-link @click="dialogVisible = true" type="primary"
            >忘记密码</el-link
          >
        </p>
        <!-- </el-form-item> -->
        <!-- <el-form-item
          style="text-align: center; border-bottom: none; color: #fff; cursor: pointer;"
        >
          若忘记密码，请联系管理员
        </el-form-item> -->
      </el-form>
    </div>
    <!-- 重置密码 -->
    <div class="repassword-panel" v-else>
      <el-form
        ref="rePasswordFormRef"
        :model="rePasswordForm"
        :rules="rules"
        @keydown.native.enter.prevent="toRepassword"
      >
        <el-form-item prop="newPass1">
          <el-input
            v-model="rePasswordForm.newPass1"
            type="password"
            maxlength="11"
            autofocus
            placeholder="设置新密码"
          ></el-input>
        </el-form-item>
        <el-form-item prop="newPass2">
          <el-input
            v-model="rePasswordForm.newPass2"
            type="password"
            maxlength="11"
            placeholder="确认新密码"
          ></el-input>
        </el-form-item>
        <!--submit button-->
        <el-form-item style="margin-top: 40px">
          <el-button
            type="primary"
            style="width: 100%; margin-top: 20px; background: #0076f6;"
            @click="toRepassword"
          >
            确认修改
          </el-button>
        </el-form-item>
        <el-form-item
          style="text-align: center; border-bottom: none; color: #fff;cursor: pointer;"
        >
          修改完成后，后续登录
        </el-form-item>
      </el-form>
    </div>

    <!-- 忘记密码 -->
    <el-dialog
      title="修改密码"
      :visible.sync="dialogVisible"
      width="600px"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      :show-close="false"
      :lock-scroll="false"
      center
    >
      <div style="width:500px;margin:auto;">
        <el-form
          ref="formPassRf"
          :model="formPass"
          :rules="rulesForm"
          label-width="130px"
          status-icon
        >
          <el-form-item label="管理员密保手机：" prop="phoneNum">
            <el-row :gutter="20">
              <el-col :span="14"
                ><el-input
                  v-model.number="formPass.phoneNum"
                  autocomplete="off"
                  disabled
                ></el-input
              ></el-col>
              <el-col :span="6"></el-col>
            </el-row>
          </el-form-item>
          <el-form-item label="验证码：" prop="checkNum">
            <el-row :gutter="20">
              <el-col :span="14"
                ><el-input
                  v-model.number="formPass.checkNum"
                  autocomplete="off"
                ></el-input
              ></el-col>
              <el-col :span="6"
                ><el-button type="primary" @click="funGetCode()">{{
                  showSecond ? `${second}s` : '发送验证码'
                }}</el-button></el-col
              >
            </el-row>
          </el-form-item>
          <el-form-item label="新密码：" prop="newPass">
            <el-row :gutter="20">
              <el-col :span="14"
                ><el-input
                  type="password"
                  autocomplete="off"
                  v-model="formPass.newPass"
                ></el-input
              ></el-col>
              <el-col :span="6"></el-col>
            </el-row>
          </el-form-item>
          <el-form-item label="新密码确认：" prop="newPassSec">
            <el-row :gutter="20">
              <el-col :span="14"
                ><el-input
                  type="password"
                  autocomplete="off"
                  v-model="formPass.newPassSec"
                ></el-input
              ></el-col>
              <el-col :span="6"></el-col>
            </el-row>
          </el-form-item>
        </el-form>
      </div>
      <div slot="footer">
        <el-button @click="resetForm('formPassRf')">取 消</el-button>
        <el-button type="primary" @click="submitForm('formPassRf')"
          >确 定</el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
// import { method } from 'cypress/types/bluebird';

import { updatePass, getCode, resetForgetPassword } from '../../api/user';
import { Row, Col, Form, FormItem, Input, Link } from 'element-ui';
export default {
  name: 'Login',
  components: {
    [Form.name]: Form,
    [FormItem.name]: FormItem,
    [Input.name]: Input,
    [Option.name]: Option,
    [Link.name]: Link,
    [Row.name]: Row,
    [Col.name]: Col
  },
  data() {
    // 手机号验证
    var checkPhoneNum = (rule, value, callback) => {
      if (!value) {
        return callback(new Error('手机号码不能为空'));
      }
      let phone = /^(((13[0-9]{1})|(15[0-9]{1})|(18[0-9]{1}))+\d{8})$/;
      setTimeout(() => {
        if (!Number.isInteger(value)) {
          callback(new Error('请输入正确的手机号码'));
        } else if (value.toString().length == 11) {
          if (phone.test(value)) {
            callback();
          } else {
            callback(new Error('请输入正确的手机号码'));
          }
        } else {
          callback(new Error('请输入正确的手机号码'));
        }
      }, 1000);
    };
    // 验证码校验
    var checkAge = (rule, value, callback) => {
      if (!value) {
        return callback(new Error('验证码不能为空'));
      }
      setTimeout(() => {
        if (!Number.isInteger(value)) {
          callback(new Error('请输入数字值'));
        } else {
          if (value.toString().length == 6) {
            callback();
          } else {
            callback(new Error('验证码为六位有效数字'));
          }
        }
      }, 1000);
    };
    // 新密码校验
    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'));
      } else {
        if (value.toString().length > 5) {
          if (this.formPass.newPassSec !== '') {
            this.$refs.formPassRf.validateField('newPassSec');
          }
          callback();
        } else {
          callback(new Error('密码长度至少为六位'));
        }
      }
    };
    // 二次输入密码校验
    var validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'));
      } else if (value !== this.formPass.newPass) {
        callback(new Error('两次输入密码不一致!'));
      } else {
        callback();
      }
    };
    return {
      loginForm: {
        userName: '',
        passWord: ''
      },
      rePasswordForm: {
        newPass1: '',
        newPass2: ''
      },
      rules: {
        username: {
          required: true,
          type: 'string',
          message: '请输入账号',
          trigger: 'blur'
        },
        password: {
          required: true,
          type: 'string',
          pattern: /^.{6,}$/,
          message: '密码至少6位',
          trigger: 'blur'
        },
        newPass1: {
          required: true,
          type: 'string',
          pattern: /^.{6,}$/,
          message: '密码至少6位',
          trigger: 'blur'
        },
        newPass2: [
          {
            validator: (rule, value, callback) => {
              if (value === '') {
                callback(new Error('请再次输入密码'));
              } else if (value !== this.rePasswordForm.newPass1) {
                callback(new Error('两次输入密码不一致!'));
              } else {
                callback();
              }
            },
            trigger: 'blur'
          }
        ]
      },
      redirect: undefined,
      otherQuery: {},
      isFirstLogin: false,
      dialogVisible: false,
      formPass: {
        phoneNum: '159****0317',
        checkNum: '',
        newPass: '',
        newPassSec: ''
      },
      showSecond: false,
      timer: null,
      second: 60,
      rulesForm: {
        // phoneNum: [{ validator: checkPhoneNum, trigger: 'blur' }],
        checkNum: [{ validator: checkAge, trigger: 'blur' }],
        newPass: [{ validator: validatePass, trigger: 'blur' }],
        newPassSec: [{ validator: validatePass2, trigger: 'blur' }]
      }
    };
  },
  watch: {
    $route: {
      handler: function(route) {
        const query = route.query;
        if (query) {
          this.redirect = query.redirect;
          this.otherQuery = this.getOtherQuery(query);
        }
      },
      immediate: true
    }
  },
  created() {
    // 跳转到单点登录页面
    // location.href = this.$store.state.settings.ssoLoginUrl;
  },
  methods: {
    // 发送验证码
    funGetCode() {
      if (this.showSecond) {
        return;
      }
      if (!this.formPass.phoneNum) {
        this.$message({
          message: '请填写手机号',
          type: 'warning'
        });
        return;
      }
      getCode({ phone: '15968510317' }).then(res => {
        if (res.status === 200) {
          this.$message({
            message: '发送成功',
            type: 'success'
          });
          this.showSecond = true;
          this.timer = setInterval(() => {
            this.second -= 1;
            if (this.second <= 0) {
              this.timer = null;
              this.second = 60;
              this.showSecond = false;
            }
          }, 1000);
        }
      });
    },
    // 忘记密码修改提交
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          resetForgetPassword({
            newPass: this.formPass.newPass,
            code: this.formPass.checkNum
          })
            .then(res => {
              this.$message({
                message: '新密码设置成功',
                type: 'success'
              });
              this.resetForm('formPassRf');
            })
            .catch(res => {});
        } else {
          this.$message({
            message: '请填写完全后提交！',
            type: 'error'
          });
          return false;
        }
      });
    },
    // 取消则重置
    resetForm(formName) {
      this.dialogVisible = false;
      this.$refs[formName].resetFields();
    },
    login() {
      this.$refs.loginFormRef.validate(async valid => {
        if (valid) {
          this.$store
            .dispatch('user/login', this.loginForm)
            .then(isFirstLogin => {
              if (isFirstLogin === true) return (this.isFirstLogin = true);
              this.$router.push({
                path: this.redirect || '/',
                query: this.otherQuery
              });
            })
            .catch(() => {})
            .finally(() => {
              this.loading = false;
            });
        } else {
          this.autoFocus();
        }
      });
    },
    toRepassword() {
      this.$refs.rePasswordFormRef.validate(valid => {
        if (valid) {
          updatePass({
            newPass: this.rePasswordForm.newPass1,
            userName: this.loginForm.username
          })
            .then(isFirstLogin => {
              if (isFirstLogin === true) return (this.isFirstLogin = true);
              this.$router.push({
                path: this.redirect || '/',
                query: this.otherQuery
              });
            })
            .catch(() => {})
            .finally(() => {
              this.loading = false;
            });
        } else {
          this.autoFocus();
        }
      });
    },
    getOtherQuery(query) {
      return Object.keys(query).reduce((acc, cur) => {
        if (cur !== 'redirect') {
          acc[cur] = query[cur];
        }
        return acc;
      }, {});
    },
    /**
     * 自动聚焦
     */
    autoFocus() {
      this.$nextTick(_ => {
        const isError = document.getElementsByClassName('is-error');
        if (isError.length > 0) {
          isError[0].querySelector('input').focus();
          return false;
        }
      });
    }
  }
};
</script>

<style lang="less" scoped>
