  background-repeat: no-repeat;
  .login-panel,
  .repassword-panel {
    position: relative;
    box-sizing: border-box;
    margin: 142px auto 220px;
    width: 680px;
    height: 540px;
    padding: 64px 0 52px;
    display: flex;
    flex-direction: column;
    // justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.44);
    border-radius: 10px;

    .login-txt {
      font-size: 36px;
      color: #ffff;
      margin-bottom: 64px;
    }
    .el-form-item {
      width: 376px;
      // margin-bottom: 16px;
    }
  }
  .repassword-panel {
    padding: 118px;
  }
}
</style>
